// VanJS Lucide - TableOfContents
import van from "vanjs-core";
import { LucideIcon, svgNamespace } from "../LucideIcon";
import { type SVGProps } from "../types";

export const TableOfContents = (props: Partial<SVGProps> = {}) => {
  const { path } = van.tags(svgNamespace);
  return LucideIcon(
    props,
    path({ "d": "M16 12H3" }),
    path({ "d": "M16 18H3" }),
    path({ "d": "M16 6H3" }),
    path({ "d": "M21 12h.01" }),
    path({ "d": "M21 18h.01" }),
    path({ "d": "M21 6h.01" }),
  );
};
